    width: 30px;
    background-size: 30px 20px;
  }
}
@media screen and (max-width: 768px) {
  .main .login-modal.opened {
    width: 90%;
    margin: 0 auto;
    border-radius: 15px;
  }
  .main .login-modal.opened .header .heading {
    font-size: 28px;
    margin-left: 30px;
  }
  .main .login-modal.opened .header svg {
    margin-top: 4%;
    margin-right: 30px;
  }
  .main .login-modal.opened input, .main .login-modal.opened input::placeholder {
    font-size: 17px !important;
  }
  .main .login-modal.opened #rememberMe + span {
    margin-left: 7%;
    min-width: 160px;
  }
  .main .login-modal.opened #rememberMe + span::before {
    width: 20px;
    height: 20px;
    border-radius: 4px;
    margin-right: 10px;
    margin-bottom: -3px;
  }
  .main .login-modal.opened #loginPassword + .hide {
    margin-top: -30px;
    width: 30px;
    background-size: 30px 20px;
  }
  .main .login-modal.opened .forget {
    font-size: 16px;
  }
  .main .login-modal.opened .button {
    font-size: 17px;
    border-radius: 40px;
    width: 80%;
    margin-right: auto;
    margin-left: auto;
  }
  .main .login-modal.opened .button + span {
    font-size: 17px;
  }
}
@media screen and (max-width: 768px) {
  .background {
    top: 82.2222222222vh !important;
    height: 87.5vh !important;
  }
}
@media screen and (max-width: 768px) and (max-height: 844px) {
  .background {
    top: calc(34.7222222222vh + 100vw) !important;
  }
}
@media screen and (max-width: 768px) {
  .cloud1, .cloud2 {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  #vulkanPoster {
    width: 100vw !important;
    height: 100vw !important;
    left: 0% !important;
    top: 43.75vh !important;
  }
}

